import { Injectable } from '@angular/core';
import { EmbedVideoService } from 'ngx-embed-video/dist';
import StretchesJson from '../../assets/stretches.json';
import IntervalsJson from '../../assets/intervals.json';
import GymPlansJson from '../../assets/gymplans.json';
import GymExercisesJson from '../../assets/exercises.json';
import {DomSanitizer} from '@angular/platform-browser';

@Injectable({
  providedIn: 'root'
})

export class Exercise {
  constructor(
    public name: string,
    public youtube_id: string,
  ) {}
}

export class Stretch {
  constructor(
    public name: string,
    public sets: number,
    public reps: number,
    public youtube_id: string,
  ) {}
}

export class Interval {
  constructor(
    public month: number,
    public details: string,
    public sessions: IntervalSession[]
  ) {}
}

export class IntervalSession {
  constructor(
    public week: number,
    public reps: number,
    public work: number,
    public rest: number
  ) {}
}

export class GymStage {
  constructor(
    public stage: string,
    public weeks: GymWeek[]
  ) {}
}

export class GymWeek {
  constructor(
    public week: number,
    public sessions: GymSession[]
  ) {}
}

export class GymSession {
  constructor(
    public stage: string,
    public week: number,
    public session: number,
    public exercises: GymExercise[]
  ) {}
}

export class GymExercise {
  constructor(
    public order: string,
    public name: string,
    public sets: number,
    public rest: number,
    public last_weight: number,
    public current_weight: number,
    public reps: number[],
    public time: number,
    public description: string
  ) {}
}


@Injectable({
  providedIn: 'root'
})
export class TrainingContentService {

  constructor(
    private embedService: EmbedVideoService,
    private sanitizer: DomSanitizer) { }

  getStretches() {

    return StretchesJson.map(s => new Stretch(
      s.name,
      s.sets,
      s.reps,
      s.youtube_id));
      // this.sanitizer.bypassSecurityTrustResourceUrl(`https://img.youtube.com/vi/${s.youtube_id}/hqdefault.jpg`),
      // this.embedService.embed_youtube(s.youtube_id)));
  }

  getIntervals() {
    return IntervalsJson.map(s => new Interval(
      s.month,
      s.details,
      s.sessions.map(k => new IntervalSession(
        k.week,
        k.reps,
        k.work,
        k.rest))));
  }

  getExercises() {
    let e= GymExercisesJson.map(e => new Exercise(
      e.name,
      e.youtube_id));
      // this.embedService.embed_youtube(e.youtube_id),
      // this.sanitizer.bypassSecurityTrustResourceUrl(`http://img.youtube.com/vi/${e.youtube_id}/hqdefault.jpg`),
      // `http://youtu.be/${e.youtube_id}`,
      // this.sanitizer.bypassSecurityTrustResourceUrl(`http://youtube.com/embed/${e.youtube_id}`)));
    return e;
  }

  getGymSession(stage:number, week:number, session:number) {
    return this.getGymStages()[stage].weeks[week].sessions[session];
  }

  getInterval(stage:number, week:number) {
    let interval = this.getIntervals()[stage];
    let sessions = interval.sessions.filter(i=>i.week == week);
    return new Interval(interval.month, interval.details, sessions);
  }

  getGymStages() {
    let plans = GymPlansJson;
    let stages = [];
    plans.map(plan => {
      let weeks = [];
      plan.weeks.map((week, i) => {
        let sessions = [];
        week.map(w => {
          let exercises = [];
          plan.plans[w].map(group => {
            group.exercises.map((exercise, j) => {
              exercises.push(new GymExercise(
                `${group.order}${j + 1}`,
                exercise.key,
                group.sets,
                j+1 < group.exercises.length ? 0 : group.rest,
                0,
                0,
                exercise.do.reps,
                exercise.do.time,
                exercise.do.description));
            });
          });
          sessions.push(new GymSession(plan.stage, i + 1, w + 1, exercises));
        });
        weeks.push(new GymWeek(i+1, sessions));
      });
      stages.push(new GymStage(plan.stage, weeks));
    });
    return stages;
  }
}
