import { Component, OnInit } from '@angular/core';
import {Stretch, TrainingContentService} from '../services/training-content.service';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'app-stretches',
  templateUrl: './stretches.component.html',
  styleUrls: ['./stretches.component.less']
})
export class StretchesComponent implements OnInit {

  stretches: any[];

  constructor(private trainingContentService: TrainingContentService, private sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.stretches = this.trainingContentService.getStretches().map(s => {
      return {
      "name": s.name,
      "reps": s.reps,
      "sets": s.sets,
      "video_img": this.youtubify(s.youtube_id),
      }
    });
    // });
  }

  youtubify(id:string) {
    this.sanitizer.bypassSecurityTrustResourceUrl(`http://img.youtube.com/vi/${id}/hqdefault.jpg`)
  }
}
