import { Component, OnInit } from '@angular/core';
import {Exercise, TrainingContentService} from '../services/training-content.service';
import {SafeUrl} from '@angular/platform-browser';

@Component({
  selector: 'app-exercises',
  templateUrl: './exercises.component.html',
  styleUrls: ['./exercises.component.less']
})
export class ExercisesComponent implements OnInit {

  exercises: Exercise[];
  images: any[];

  constructor(private trainingContentService: TrainingContentService) {}

  ngOnInit(): void {

    this.images = this.trainingContentService.getExercises().map(e => {
      return {
        source: `http://img.youtube.com/vi/${e.youtube_id}/hqdefault.jpg`,
        thumbnail:`http://img.youtube.com/vi/${e.youtube_id}/default.jpg`,
        title: e.name
      }});
    this.exercises = this.trainingContentService.getExercises();
  }
}
