import { Component, OnInit } from '@angular/core';
import {Interval, TrainingContentService} from '../services/training-content.service';

@Component({
  selector: 'app-intervals',
  templateUrl: './intervals.component.html',
  styleUrls: ['./intervals.component.less']
})
export class IntervalsComponent implements OnInit {

  intervals: Interval[];

  constructor(private trainingContentService: TrainingContentService) {}

  ngOnInit(): void {

    this.intervals = this.trainingContentService.getIntervals();
  }
}
